*{
    padding: 0;
    margin: 0;
}

body {
    background-image: url(../img/Fondo.jpg);
    
}

.Calendario {
    text-align: center;
    background-color: #3e0350;
    color: white;
    padding: 20px;
    font-family: 'Times New Roman', Times, serif;
    font-size: 50px;
}

nav ul {
    background-color: #ca9dd9b6;
    width: 100%;
    list-style-type: none;
    text-align: center;
}

nav li {
    display: inline-flex;
}

nav a {
    text-decoration: none;
    color: rgb(252, 252, 252);
    padding: 10px;
    width: 150px;
}

nav li:hover {
    background-color: #8d6d99b6;
}


footer {
    width: 100%;
    height: 200px;
    background-color: #3e0350;
    color: white;
    margin-top: 40px;
}

footer p {
    text-align: center;
    padding: 50px;
}

.container_calendar{
    width: 400px;
    height: 550px;
   /*  background: salmon; */
    margin: 5% auto;
    
  }

  .header_calendar{
    width: 100%;
    height: 45%;
    background: linear-gradient(#7F99F5, #506CF5);
    border-radius: 15px 15px 0px 0px;
    display: flex;
    flex-direction: column;
    h1{
      margin: 30px 30px 0px 30px;
      padding: 0px;
      font-family: Arial;
      color: white;
      font-size: 100px;
    }
    h5{
      margin: 0px 30px;
      padding: 0px;
      font-family: Arial;
      color: white;
      font-size: 40px;
  
   }
  }

  .body_calendar{
    width: 100%;
    height: 55%;
    background: white;
    border-radius: 0px 0px 15px 15px;
  }
  .container_details{
    width: 100%;
    height: 30px;
    /* background: #000000; */
    position: relative;
    top: -13px;
    display: flex;
    justify-content: space-between;
  
    .detail_1, .detail_2{
      width: 70px;
      height: 100%;
      /* background: salmon; */
      display: flex;
      justify-content: center;
      .detail{
        margin: 0px 2px;
        width: 10px;
        height: 100%;
        /* background:red; */
        .circle{
          width: 10px;
          height: 10px;
          background: #1C1E26;
          position: relative;
          top: calc(30px - 10px);
          border-radius: 50%;
          .column{
            width: 80%;
            height: 30px;
            background: #ececec;
            margin: 0px auto;
            position: relative;
            top: -22px;
            border-radius: 5px;
          }
        }
      }
      }
  }
  .container_change_month{
    width: 100%;
    height: 25px;
    /* background: gold; */
    display: flex;
    justify-content: space-evenly;
    opacity: 0.7;
    div{
      /* background: firebrick; */
      height: 20px;
      margin: auto;
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    }
    button{
      font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
      font-size: 20px;
      margin: auto 5px;
      border: none;
      background: none;
      cursor: pointer;
    }
  }
  .container_weedays{
    width: 90%;
    height: 40px;
    /* background: #fa80725e; */
    display: flex;
    margin: 0px auto;
    span{
      color: #506CF5;
      font-family: Verdana, Geneva, Tahoma, sans-serif;
      height: 100%;
      line-height: 2.5;
    }
  }
  .week_days_item{
    display: flex;
    /* background: firebrick; */
    margin: auto;
    width: calc(100% / 7);
    justify-content: center;
    cursor: pointer;
  }
  .week_days_item:hover{
    color: #506CF5;
  }
  .prev_days{
    opacity: 0.5;
  }
  .container_days{
    width: 90%;
    height: auto;
    /* background: darkcyan; */
    display: flex;
    flex-flow: row wrap;
    margin: 0px auto;
    span{
      color: rgb(78, 78, 78);
      /* background: #000; */
      height: 20px;
      margin: 5px 0px;
      padding: 0px;
      font-family: Arial;
      
    }
    .today{
      display: flex;
    }
    .today:after{
      content: "";
      width: 19px;
      height: 19px;
      background: #506CF5;
      position: absolute;
      border-radius: 50%;
      border: 5px solid #506CF5;
      opacity: 0.6;
      margin: -5px 0px 0px 1px;
  }
  }

  .bienvenida-container {
    position: relative;
    text-align: center;
    padding: 20px;
    background-color: #3e0350;
    color: white;
}

.bienvenida {
    position: relative;
    z-index: 1;
}

.icono {
    position: absolute;
    top: 10px;
    right: 50px;
    font-size: 24px;
    cursor: pointer;
    z-index: 2;
}

.menu-lista {
    display: none;
    position: absolute;
    top: 40px;
    right: 10px;
    background-color: rgb(198, 141, 202);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    list-style: none;
    padding: 0;
    margin: 0;
    border-radius: 4px;
    z-index: 3;
}

.menu-lista li {
    padding: 8px 16px;
}

.menu-lista li a {
    text-decoration: none;
    color: black;
}

.menu-lista li:hover {
    background-color: #874c84;
}

.tabla-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    border: 1px solid #ddd;
    z-index: 1;
    width: 80%;
}

table {
    width: 100%;
    border-collapse: collapse;
}

th, td {
    padding: 8px;
    text-align: left;
    border-bottom: 1px solid #ddd;
}

th {
    background-color: #f2f2f2;
}


.Icono2 {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  z-index: 2;
}


.menu-listado {
  display: none;
  position: absolute;
  top: 40px;
  right: 10px;
  background-color: rgb(198, 141, 202);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  list-style: none;
  padding: 0;
  margin: 0;
  border-radius: 4px;
  z-index: 3;
}

.menu-listado li {
  padding: 8px 16px;
}

.menu-listado li a {
  text-decoration: none;
  color: black;
  display: block;
}

.menu-listado li:hover {
  background-color: #874c84;
}